    -webkit-appearance:none;
}
/*customize the scroll bar*/
::-webkit-scrollbar {
    width: 6px;
    height: 5px;
}
::-webkit-scrollbar-track-piece {
    -webkit-border-radius: 6px;
}
::-webkit-scrollbar-thumb:vertical {
    height: 5px;
    background: #e5e5e5;
    -webkit-border-radius: 6px;
}
